<template>
  <div class="card col-12 my-3 mx-0" :class="isFirst && 'border-success'">
    <div class="card-body">
      <div class="row mx-0 mb-2">
        <h4 class="card-title">{{ item.name }}</h4>
        <h5 class="card-subtitle mention ml-auto mt-1">{{ mention }}</h5>
      </div>
      <div v-if="voteStatus == '1'" class="d-flex w-100 justify-content-between">
        <font-awesome-icon :icon="['far', 'frown']" class="text-danger" />
        <div v-for="(v, k) in mentions" :key="item._id + '_' + v">
          <input
            type="radio"
            :name="item._id + '_' + v"
            :value="k"
            v-model="pickedMention"
            @click="hasAlreadyVoted ? undefined : updateBulletin(k)"
          />
        </div>
        <font-awesome-icon :icon="['far','smile']" class="text-success" />
      </div>
      <div v-else>
        <div class="repere"></div>
        <!-- <font-awesome-icon :icon="['fas', 'caret-up']" class="text-white repere" /> -->
         <div class="nmb_voices_bar">
          <div
          v-for="(k, v, index) in mentions"
          :style="{ width: getWidth(index+1) + '%' }"
          :key="item._id + '_' + k"
        ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CalcVote from '@/mixins/calc-vote.vue';

export default {
  name: "Proposition",
  props: {
    item: {
      type: Object,
      required: true
    },
    voteStatus: String,
    hasAlreadyVoted: Boolean,
    childToParent: Function,
    choice_value: String,
    isFirst: Boolean,
    voteIndex: Number
  },
  mixins: [CalcVote],
  data: function() {
    return {
      mentions: {
        nb1: "À rejeter",
        nb2: "Insuffisant",
        nb3: "Passable",
        nb4: "Assez bien",
        nb5: "Bien",
        nb6: "Très bien"
      },
      pickedMention: this.hasAlreadyVoted ? this.choice_value : "nb6"
    };
  },
  computed: {
    mention: function() {
      return (this.voteStatus < 2 ? this.mentions[this.pickedMention] : this.mentions["nb" + (this.getMajorityMention(this.item) + 1)]);
    }
  },
  methods: {
    updateBulletin(value) {
      this.$emit("childToParent", this.item._id, value);
    },
    getWidth(k) {
      let total = 0;
      for (let i = 1; i <= 6; i++) {
        total += (parseInt(this.item["nbNote" + i]) || 0);
      }
      const width = ((parseInt(this.item["nbNote" + k]) || 0) * 100) / total;
      return width.toString();
    }
  }
};
</script>

<style lang="scss" scoped>
.nmb_voices_bar {
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: row;
  border-radius: 8px;
  position: relative;
}

.nmb_voices_bar > div:not(.repere) {
  height: 100%;
}

.nmb_voices_bar > div:nth-child(1):not(.repere) {
  background-color: red;
}

.nmb_voices_bar > div:nth-child(2):not(.repere) {
  background-color: orange;
}

.nmb_voices_bar > div:nth-child(3):not(.repere) {
  background-color: yellow;
}

.nmb_voices_bar > div:nth-child(4):not(.repere) {
  background-color: greenyellow;
}

.nmb_voices_bar > div:nth-child(5):not(.repere) {
  background-color:  yellowgreen;
}

.nmb_voices_bar > div:nth-child(6):not(.repere) {
  background-color: green;
}

.repere {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0px;
  height: 80px;
  border: 1px white dashed;
  z-index: 20;
  transform: translateX(-50%);
}

</style>
