<template>
  <!-- <div> -->
    <main-card-container v-if="loading">
      <div slot="content">
        Chargement...
      </div>
    </main-card-container>
    <main-card-container v-else :title="vote.title" :copyUrl="vote.isAnonymous" :secondTitle="vote.isAnonymous ? 'Pour ne pas perdre ce vote, pensez à l\'enregistrer !' : '' " :addInFavoris="vote.isAnonymous">
      <div slot="content" class="col-8">
        <p class="my-4 text-justify" v-html="vote.description"></p>
        <h5 class="text-left">
          {{vote.status != 1 ? "Liste des vainqueurs" : `${vote.nbChoices} proposition${ vote.nbChoices > 1 && "s"}`}}
        </h5>
        <form id="choicesForm" @submit="submitVote">
          <Proposition
            v-for="(choice, index) in choices"
            :key="index"
            :item="choice"
            :voteStatus="vote.status"
            :hasAlreadyVoted="false"
            :choice_value="'nb6'"
            v-on:childToParent="updateBulletin"
            :isFirst="index == 0 && vote.status != 1"
            :voteIndex="index + 1"
          />
          <!-- <Proposition
            v-for="(choice, index) in choices"
            :key="index"
            :item="choice"
            :voteStatus="vote.status"
            :hasAlreadyVoted="hasAlreadyVoted"
            :choice_value="hasAlreadyVoted ? ballot.choices_values[choice._id].value : 'nb6'"
            v-on:childToParent="updateBulletin"
          /> -->
          <div class="d-flex flex-column">
            <button
              v-if="vote.status == 1"
              type="submit"
              class="btn btn-primary d-block ml-auto"
            >A voté !</button>
            <button
              v-if="vote.status == 1 && currentUser && vote.user_id == currentUser._id"
              :disabled="vote.nbBulletins == 0"
              id="closeVote"
              type="button"
              class="btn btn-outline-danger d-block"
              @click="closeVote(vote._id)"
            >Clôturer le vote</button>
          </div>
        </form>
      </div>
    </main-card-container>
</template>

<script>
import Proposition from "@/components/Proposition";
import ApiCalls from "@/mixins/api-calls.vue";
import CalcVote from "@/mixins/calc-vote.vue";
import MainCardContainer from '../components/MainCardContainer.vue';

export default {
  name: "Vote",
  components: {
    Proposition,
    MainCardContainer
  },
  mixins: [ApiCalls, CalcVote],
  data: function() {
    return {
      currentUser: JSON.parse(localStorage.getItem('currentUser')),
      hasAlreadyVoted: false,
      ballot: {},
      vote: {},
      choices: [],
      choices_values: {},
      loading: true
    };
  },
  created() {
    this.findRecord("votes", {
        filter: {
          _id: this.$route.params.vote_id
        }
      })
      .then(response => {
        this.vote = response.data.entries[0];
        this.hasAlreadyVoted = localStorage.getItem(this.vote._id) || false;
        this.findRecord('choices', {
              filter: {
                vote_id: this.$route.params.vote_id
              }
            })
          .then(responseChoices =>  {
            this.choices = responseChoices.data.entries;
            if (this.vote.status > 1) {
              console.log(this.choices);
              this.sort_choices(this.choices);
            }
            responseChoices.data.entries.forEach(choice => {
              this.choices_values[choice._id] = {
                name: choice.name,
                value: "nb6"
              };
            });
            this.loading = false;
          })
          .catch(e => console.error(e));
      })
      .catch(e => console.error(e));
    if (this.currentUser) {
      this.findRecord('ballots', {
        filter: {
          vote_id: this.$route.params.vote_id,
          voting_user_id: this.currentUser._id
        }
      })
      .then(response => {
        if (response.data.entries.length > 0) {
          this.ballot = response.data.entries[0];
          this.hasAlreadyVoted = true;
        }
      }).catch(e => console.error(e));
    }
  },
  methods: {
    updateBulletin(choice_id, value) {
      this.choices_values[choice_id].value = value;
    },
    async submitVote(e) {
      e.preventDefault();
      // création du bulletin
      this.createOrSaveRecord('ballots', {
        vote_id: this.vote._id,
        voting_user_id: this.currentUser ? this.currentUser._id : 'ANONYM',
        choices_values: this.choices_values
      }).catch(e => console.error(e));
      // Maj choix
      for(const choice of this.choices) {
        const chosenMentionNmb = this.choices_values[choice._id]['value'].replace("nb", '');
        const colName = "nbNote" + chosenMentionNmb;
        let params = { _id: choice._id };
        params[colName] = ((parseInt(choice[colName]) || 0) + 1).toString();
        await this.createOrSaveRecord('choices', params).catch(e => console.error(e));
      }
      // Maj du vote
      this.createOrSaveRecord('votes', {
        _id: this.vote._id,
        nbBulletins: parseInt(this.vote.nbBulletins) + 1
      })
      .then(() => {
        if (this.currentUser) {
          this.$router.push('/account/' + this.currentUser._id);
        } else {
          localStorage.setItem(`${this.vote._id}`, true);
          this.hasAlreadyVoted = true
        }
        
      })
      .catch(e => console.error(e));
    },
    closeVote(vid) {
      const winning_id = this.sort_choices(this.choices)[0]._id
      this.createOrSaveRecord('votes', {
        _id: vid,
        winningChoice_id: winning_id,
        status: 2
      })
      .then(() => this.$router.push('/account/' + this.currentUser._id))
      .catch(e => console.error(e));
    }
  }
};
</script>

<style lang="scss"></style>
