    // /**
    // * Repeats some text a given number of times.
    // *
    // * @param {array} gradesTally - The number of bulletin per mention
    // * @param {boolean} favorContestation - true with mention maj
    // */

    getMajorityMention(choice, favorContestation = false) {
      let gradesTally = [];
      for (let i = 1; i < 7; i++) {
        gradesTally.push(parseInt(choice["nbNote" + i]) || 0);
      }
      const amountOfGrades = gradesTally.length;
      const total = gradesTally.reduce((a, b) => a + b);
      console.log(total);
      const medianOffset = favorContestation ? 1 : 2;
      let foundMedian = false;
      let medianGrade = 0;
      const median = Math.floor((total + medianOffset) / 2);
      let cumulatedGrades = 0;

      for (let grade = 0; grade < amountOfGrades; grade++) {
        cumulatedGrades += gradesTally[grade];
        if (!foundMedian) {
          if (cumulatedGrades > median) {
            foundMedian = true;
            medianGrade = grade;
          }
        }
      }

      return medianGrade;
    },

    analyse(gradesTally, favorContestation) {
      const amountOfGrades = gradesTally.length;
      const total = gradesTally.reduce((a, b) => a + b);
      const medianOffset = favorContestation ? 1 : 2;
      const median = Math.floor((total + medianOffset) / 2);

      let cumulatedGradesBefore = 0;
      let cumulatedGrades = 0;
      let foundMedian = false;

      let results = {
        medianGrade: 0,
        medianGradeGroupSize: 0,
        adhesionGrade: 0,
        adhesionGroupSize: 0,
        contestationGrade: 0,
        contestationGroupSize: 0,
        secondMedianGrade: 0,
        secondMedianGroupSize: 0,
        secondMedianGroupSign: 0
      };

      for (let grade = 0; grade < amountOfGrades; grade++) {
        const gradeTally = gradesTally[grade];
        cumulatedGradesBefore = cumulatedGrades;
        cumulatedGrades += gradeTally;

        // determine grade, adhesionGroupSize, adhesionGrade, contestationGrade and contestationGroupSize
        if (!foundMedian) {
          if (cumulatedGrades > median) {
            foundMedian = true;
            results.medianGrade = grade;
            results.medianGradeGroupSize = gradeTally;
            results.contestationGroupSize = cumulatedGradesBefore;
            results.adhesionGroupSize = total - cumulatedGradesBefore - gradeTally;
          } else {
            if (gradeTally > 0) results.contestationGrade = grade;
          }
        } else {
          if (gradeTally > 0 && results.adhesionGrade == 0) results.adhesionGrade = grade;
        }
      }

      results.secondMedianGroupSize = Math.max(results.adhesionGroupSize, results.contestationGroupSize);
      if (results.contestationGroupSize < results.adhesionGroupSize) {
        results.secondMedianGrade = results.adhesionGrade;
        results.secondMedianGroupSign = 1;
      } else if (results.contestationGroupSize > results.adhesionGroupSize) {
        results.secondMedianGrade = results.contestationGrade;
        results.secondMedianGroupSign = -1;
      } else {
        if (favorContestation) {
          results.secondMedianGrade = results.contestationGrade;
          results.secondMedianGroupSign = -1;
        } else {
          results.secondMedianGrade = results.adhesionGrade;
          results.secondMedianGroupSign = 1;
        }
      }

      if (results.secondMedianGroupSize == 0) results.secondMedianGroupSign = 0;

      return results;
    },

    moveJudgments(gradesTally, fromGrade, intoGrade) {
      let newTally = Array.from(gradesTally);
      newTally[intoGrade] = gradesTally[intoGrade] + gradesTally[fromGrade];
      newTally[fromGrade] = 0;
      return newTally;
    },

    generateScore(gradesTally, favorContestation) {
      const amountOfGrades = gradesTally.length;
      let grades = Array.from(gradesTally);

      let scores = [];
      for (let grade = 0; grade < amountOfGrades; grade++) {
        const results = this.analyse(grades, favorContestation);
        let score = {
          grade: results.medianGrade,
          secondGroupScore: results.secondMedianGroupSign * results.secondMedianGroupSize
        };
        scores.push(score);
        grades = this.moveJudgments(grades, results.medianGrade, results.secondMedianGrade);
      }

      return scores;
    },

    compareChoices(choices1, choices2) {
      let gradesTally1 = [];
      let gradesTally2 = [];
      for (let i = 1; i < 7; i++) {
        gradesTally1.push(parseInt(choices1["nbNote" + i]) || 0);
        gradesTally2.push(parseInt(choices2["nbNote" + i]) || 0);
      }
      const scores1 = this.generateScore(gradesTally1, true);
      const scores2 = this.generateScore(gradesTally2, true);
      const amountOfGrades = gradesTally1.length;
      let i = 0;
      while (i < amountOfGrades) {
        let score1 = scores1[i];
        let score2 = scores2[i];
        if (score1.grade > score2.grade) return -1;
        else if (score1.grade < score2.grade) return 1;
        else {
          if (score1.secondGroupScore > score2.secondGroupScore) return -1;
          else if (score1.secondGroupScore < score2.secondGroupScore) return 1;
        }
        i++;
      }
      return 0;
    },

    sort_choices(choices) {
      return choices.sort((a, b) => this.compareChoices(a, b));
    }
  }
};
