        <h2 class="mb-5">Les défauts du système de vote classique</h2>
        <p class="d-inline">
          En France, nous avons l’habitude de voter avec le mode de scrutin dit “uni-nominatif à 1 ou 2 tours”.
          <br><br>
          Pour rappel : parmi plusieurs propositions, un votant donne 1 unique voix “pour” à la proposition de son choix, la proposition avec le plus de voix l’emporte, un 2ème tour sert à créer une majorité absolue entre les 2 propositions en tête du 1er tour.
