          <li>Le “vote utile” ou stratégique parasite nos choix objectifs</li>
          <li>Deux propositions proches divisent les voix entre elles et perdent toutes les deux</li>
          <li>Les voix de contestations sont ignorées (blanc ou abstention)</li>
          <li>La lecture trop binaire de l’opinion des votants, manque de nuance</li>
        </ul>
        <p>
          Au 21ème siècle à l’heure des algorithmes ultra-complexe c’est difficilement défendable.
          <br><br>
          Imaginez simplement que l’on puisse mettre 1 bulletin “Contre” en plus d’1 bulletin “Pour”. 
          <br>
          Ou bien qu'au lieu d’1 proposition on puisse voter pour 2 ou 3. 
          <br>
          Ou bien qu’on puisse voter comme au cinéma des notes nuancées de 1 à 5 étoiles.
          <br><br>
          Les élections auraient des résultats très différents.
